<template>
  <div id="app">
    <ul v-if="producto && producto.length">
      <li v-for="productos of producto">
        <p><strong>{{productos.id}}</strong></p>
        <p>{{productos.name}}</p>
        <p>{{productos.description}}</p>
        <p>{{productos.category_name}}</p>
        <b-img thumbnail left fluid :src="productos.image" width="150" height="150" alt="Thumbnail" />
      </li>
    </ul>

    <ul v-if="errors && errors.length">
      <li v-for="error of errors">
        {{error.message}}
      </li>
    </ul>
  </div>
</template>

<script>
  /*eslint-disable*/
  import axios from 'axios';

  export default {
    name: "api",
    data () {
      return {
        producto: [],
        errors: []
      }
    },
    mounted () {
      axios
        .get('https://apirestphp-cayola.c9users.io/product/read.php')
        .then(response => {
          // JSON responses are automatically parsed.
          this.producto = response.data.records
        }).catch(e => {
        this.errors.push(e)
      })
    }
}
  /*eslint-disable*/
</script>

<style scoped>

</style>



