<template>
  <div app="card">
    <v-app id="inspire">
      <v-layout row wrap>
        <v-card v-if="producto && producto.length">
          <v-flex v-for="productos of producto" sm12 md4->
                <v-card-media :src="productos.image" height="200px"></v-card-media>
                <v-card-title primary-title>
                  <div>
                    <div class="headline">{{productos.name}}</div>
                    <span class="grey--text">Precio : {{productos.price}}</span>
                  </div>
                </v-card-title>
                <v-card-actions>
                  <v-btn flat to="/detalle">Detalle</v-btn>
                  <p>{{productos.category_name}}</p>
                  <v-btn flat color="purple">Ver</v-btn>
                  <v-spacer></v-spacer>
                  <v-btn icon @click.native="show = !show">
                    <v-icon>{{ show ? 'keyboard_arrow_down' : 'keyboard_arrow_up' }}</v-icon>
                  </v-btn>
                </v-card-actions>
                <v-slide-y-transition>
                  <v-card-text v-show="show">
                    {{productos.description}}
                  </v-card-text>
                </v-slide-y-transition>

              </v-flex>
        </v-card>
      </v-layout>
    </v-app>
    <ul v-if="errors && errors.length">
      <li v-for="error of errors">
        {{error.message}}
      </li>
    </ul>
  </div>
</template>

<script>
  /*eslint-disable*/
  import axios from 'axios';

  export default {
    name: "card",
    data () {
      return {
        show: false,
        producto: [],
        errors: []
      }
    },
    mounted () {
      axios
        .get('https://apirestphp-cayola.c9users.io/product/read.php')
        .then(response => {
          // JSON responses are automatically parsed.
          this.producto = response.data.records
        }).catch(e => {
        this.errors.push(e)
      })
    }
  }
  /*eslint-disable*/
</script>

<style scoped>

</style>



